
.formControl.error div.errorSign 
{
    display: inline-block;
    color: white;
    font-size: 15px;
    background-color: #EB001C;
    width: 20px;
    height: 20px;
    text-align: center;
    border-radius: 23px;
    margin-left: 5px; 
    bottom: 10px; 
      
}

.tooltip {
    position: relative;
}

.tooltip::before {
    display: block;
    position: absolute;
    width: 150px;
    background-color: #EB001C;
    content: attr(data-title);
    bottom: 0;
    right: 50%;
    margin-right: -165px;
    padding: 8px;
    box-sizing: border-box;
    text-align: center;
    font-size: 15px;
    border-radius: 5px;

    transition: opacity 0.7s;
    visibility: hidden;
    opacity: 0;
}

.tooltip:hover::before {
    visibility: visible;
    opacity: 0.8;
}


