.nav {
    grid-area: n;
    background-color: #292e35;
    padding: 20px;
  }

.item {
  /* font-size: 20px; */
  list-style-type: none;
  margin: 10px;
}

.item a{
  color: #ffffff;
  text-decoration: none;
}

.item a.activeLink{
  color: #ff9e00;
}

.friendsItem {
  color: #ffffff;
}

.friendsItem ul{
  color: #ffffff;
  list-style-type: none;
  font-size: 15px;
  margin-top: 20px;
  padding-left: 20px;
}

.friendsItem img {
  width: 40px;
  height: 40px;
  border-radius: 50px;
  float: left;
}


.friendsItem ul li p { 
  display: inline-block;
  margin-left: 10px;
  margin-top: 18px;
}

.friendsItem ul li {
  margin-bottom: 5px;
}



